	module.exports = {"data":{"markdownRemark":{"html":"<h2>Area of a Square</h2>\n<p>A square is a quadrilateral with identical sides.</p>\n<p>The area of a square can be calculated either from the length of the side, or from the length of the diagonal.</p>\n<p><img src=\"https://github.com/viso-gio/HostedImages/blob/master/square.jpg?raw=true\" alt=\"Square\"></p>\n<p>If the side <em>a</em> is known:</p>\n<p><img src=\"https://github.com/viso-gio/HostedImages/blob/master/AreaSquare1.png?raw=true\" alt=\"Formula1\"></p>\n<p>If the diagonal <em>d</em> is known:</p>\n<p><img src=\"https://github.com/viso-gio/HostedImages/blob/master/AreaSquare2.png?raw=true\" alt=\"Formula2\"></p>\n<p>The relationship between side <em>a</em> and diagonal <em>d</em> is:</p>\n<p><img src=\"https://github.com/viso-gio/HostedImages/blob/master/DiagonalSquare.png?raw=true\" alt=\"Diagonal\"></p>\n<h3>Terminology</h3>\n<p>Diagonal - the distance from opposite corners of a square. The corners are 90-degrees.</p>\n<p>Perimeter - the sum of the length of four sides in a square. The four sides are equal in a square.</p>\n<h3>Examples</h3>\n<ol>\n<li>\n<p>Given length of one side of square is 8 units:</p>\n<p>Area = 8 x 8 = 64 units<sup>2</sup></p>\n</li>\n<li>\n<p>Given area of a square is 100 units<sup>2</sup>:</p>\n<p>Length = <span>√</span>100 = 10 units</p>\n</li>\n<li>\n<p>Given diagonal of square is 25 units:</p>\n<p>Area = 10<sup>2</sup> / 2 = 50 units<sup>2</sup></p>\n</li>\n<li>\n<p>Given perimeter of a square is 20 units:</p>\n<p>Area = (20 / 4)<sup>2</sup> = 25  units<sup>2</sup></p>\n</li>\n</ol>\n<h4>More Information:</h4>\n<ul>\n<li><a href='https://en.wikipedia.org/wiki/Square#Perimeter_and_area' target='_blank' rel='nofollow'>Wikipedia: Square</a></li>\n<li><a href='https://www.wikihow.com/Find-the-Area-of-a-Square' target='_blank' rel='nofollow'>More Illustrations</a></li>\n</ul>","fields":{"slug":"/mathematics/area-of-a-square/"},"frontmatter":{"title":"Area of a Square"}}},"pathContext":{"slug":"/mathematics/area-of-a-square/"}}

